import React from "react";
import { NavLink, Link } from "react-router-dom";
import "./Navbar.css";

class Navbar extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      collapseMobileNav: false,
    };
  }

  mobileNavbarCollapser = () => {
    this.setState({ collapseMobileNav: false });
  };

  displayNavbar = () => {
    document.querySelector(".navTextContainerMobile").style.display = "flex";
  };

  hideNavbar = () => {
    if (this.state.collapseMobileNav === true) {
      document.querySelector(".navTextContainerMobile").style.display = "flex";
      this.setState({ collapseMobileNav: false });
    } else {
      document.querySelector(".navTextContainerMobile").style.display = "none";
    }
  };
  render() {
    return (
      <div className="header">
        <div className="logo"> Mundostar </div>
        <ul className="navBar">
          <NavLink className="navLink" to="/">
            Home
          </NavLink>
          <NavLink className="navLink" to="/Pages/about/">
            About us
          </NavLink>
          <NavLink className="navLink" to="/Pages/our/">
            Our Team
          </NavLink>
          <NavLink className="navLink" to="/Pages/contact/">
            Contact
          </NavLink>
        </ul>
        <ul className="navBarMobile" onClick={this.displayNavbar}>
          <li className="line"> </li> <li className="line"> </li>
          <li className="line"> </li>
        </ul>
        <div className="navTextContainerMobile">
          <button className="MobileMenuOff" onClick={this.hideNavbar}>
            x
          </button>
          <NavLink
            className="navLink"
            to="/"
            onClick={this.hideNavbar}
          >
            Home
          </NavLink>
          <NavLink className="navLink" to="/Pages/about/" onClick={this.hideNavbar}>
            About us
          </NavLink>
          <NavLink className="navLink" to="/Pages/our/" onClick={this.hideNavbar}>
            Our Team
          </NavLink>
          <NavLink className="navLink" to="/Pages/contact/" onClick={this.hideNavbar}>
            Contact
          </NavLink>
        </div>
      </div>
    );
  }
}

export default Navbar;
